body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.friend-activity-card {
    text-align: left;
    max-width: 200px;
    min-width: 150px;
    padding: 12px 8px 12px 8px;
}

.friend-activity-card .friend-activity-leftside {
    float: left;
    width: 80%;
    font-size: calc(8px + 2vmin);
}

.friend-activity-card .friend-activity-rightside {
    float: right;
    width: 20%;
}

.friend-activity-card .friend-activity-name {
    font-weight: bold;
    font-size: calc(12px + 2vmin);
}

.friend-activity-card .friend-activity-timestamp {
    float: right
}

.friend-activity-album {
    padding-top: 5px;
}

.friend-activity-song {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;    
}

.friend-activity-timestamp {    
    font-size: calc(8px + 2vmin);
}
